import { Component,ViewChild  } from '@angular/core';
import { ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';
import { IonicSelectableComponent } from 'ionic-selectable';

@IonicPage()
@Component({
  selector: 'page-Permiso3-create',
  templateUrl: 'Permiso3-create.html',
})
export class Permiso3CreatePage {
  @ViewChild('portComponent') portComponent: IonicSelectableComponent;
  ports: any[];
  port: any;
  public form                  : FormGroup;
  username: string = "";
  public nidrol                 : any;
  public idrol                 : any;
  public idpermiso                 : any;
  public idmodulo                 : any;
  public orden                 : any;
  public ingreso                 : any;
  public potros1                 : any;
  
  public consulta	     : any;
  public eliminacion		     : any;
  public filtro	     : any;
  public reporte	     : any;
  public creacion	     : any;
  public actualizacion		     : any;
  idempresa:any;

  public nmodulo :any;
  public vgrupo  :any=[];
 
  private _ID                  : String;

  public pageTitle           	: string;
  createdCode = null;
  scannedCode = null;

  isDisablednit :any;
  constructor(public storage: Storage,  public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {

    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
     
  })
     // Define form validation - VERY basic!
     this.form = this._FB.group({
        idmodulo       : ['', Validators.required],
        orden       : ['', Validators.required],
       ingreso : [''],
       consulta : [''],
       eliminacion : [''],
       creacion : [''],
       actualizacion : [''],
       filtro:[''],
       reporte:[''],
       potros1:['']
      
     });

 

  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }

  ionViewDidLoad() : void
  {this.idrol=this.navParams.data.record2.idrol;
    this.nidrol=this.navParams.data.record2.nidrol;
    this.idpermiso=this.navParams.data.record2.idpermiso;
     
    
    if(this.navParams.get("record")==null)
    {
      this.authService.getReg('todos/todos/'+this.idempresa,'modulos').then((result) => {
        this.vgrupo=result;
      }, (err) => {  
      this.msj.showError3('Error',err.error);
    });
      this.authService.getReg(this.idempresa+'/'+ this.idrol +'/'+ this.idpermiso+'/orden','permiso3s').then((data) => {
        if(data)
        {
          var aa:any=data;
           
          this.orden=aa.orden;
          this.orden=this.orden+10;
        }
  }, (err) => {
    this.msj.showError3('Error',err.error);
   // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })  
  });


      this.limpia('true');   
      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
      
      this.isDisablednit =true
    }
    else {
      this.limpia('false');
       this._ID 				=	this.navParams.data.record._id;
       this.idrol		=	this.navParams.data.record.idrol;
       this.authService.getReg('todos/todos/'+this.idempresa,'modulos').then((result) => {
        this.vgrupo=result;
        this.idmodulo		=	this.navParams.data.record.idmodulo;
        this.orden		=	this.navParams.data.record.orden;
      }, (err) => {  
      this.msj.showError3('Error',err.error);
    });
      
       this.ingreso		=	this.navParams.data.record.ingreso;
       this.consulta		=	this.navParams.data.record.consulta;
       this.eliminacion		=	this.navParams.data.record.eliminacion;
       this.creacion		=	this.navParams.data.record.creacion;
       this.actualizacion		=	this.navParams.data.record.actualizacion;
       this.filtro		=	this.navParams.data.record.filtro;
       this.reporte		=	this.navParams.data.record.reporte;

       this.potros1		=	this.navParams.data.record.potros1;

       
      
       this.isDisablednit =false
       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    

    }
  }

  limpia(op:string) : void
  {
    this.form.controls['idmodulo'].setValue('');
    this.form.controls['orden'].setValue('');
    this.form.controls['ingreso'].setValue(op);
     this.form.controls['consulta'].setValue(op);
     this.form.controls['eliminacion'].setValue(op);
     this.form.controls['creacion'].setValue(op);
    this.form.controls['actualizacion'].setValue(op);
    this.form.controls['filtro'].setValue(op);
    this.form.controls['reporte'].setValue(op);
  }
    

 
  

  manageGallery() : void
  {
    
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }
   


     let   options       : any	     = { 
      idempresa : this.idempresa,
      idpermiso : this.idpermiso,
      idrol : this.idrol,
     nombre : this.form.controls['idmodulo'].value,
     ingreso : this.form.controls['ingreso'].value,
     consulta : this.form.controls['consulta'].value,
     eliminacion : this.form.controls['eliminacion'].value,
     creacion : this.form.controls['creacion'].value,
     actualizacion: this.form.controls['actualizacion'].value,
     filtro : this.form.controls['filtro'].value,
     reporte : this.form.controls['reporte'].value,
     orden: this.form.controls['orden'].value,
     potros1: this.form.controls['potros1'].value,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,Permiso3:creat,accion:creat+' Permiso3s '}
    };


     if(this.navParams.get("record")==null)
     {  
          if(options){
            this.authService.createReg('',options,'Permiso3s').then((result) => {
                 if (!this.form.valid) { return; }
                 this.viewCtrl.dismiss(result);
         
          }, (err) => {  
            this.msj.showError3('Error',err.error);
          
          });
          }
     }   
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'Permiso3s').then((result) => {
           if (!this.form.valid) { return; }
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }

  

        
  portChange(event: {
    component: IonicSelectableComponent,
    value: any 
  }) {

  //  this.idprofesortt=event.value._id;


  }

}



